.header {
  width: 100%;
  height: 300px;
  background: lightcoral;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding: 0 10px 30px;
  background: url('../img/photos/cosmic_rose_essence_by_ellysiumn-dcccim4.jpg') no-repeat center / cover;
}
.person {
  display: flex;
  width: 50%;
  align-items: center;
}
.person-info {
  margin-left: 15px;
}
.person-info__container {
  background: #fff;
  border-radius: 10px;
  padding: 5px 15px;
}
.person-info__name {
  margin: 5px;
  font-size: 20px;
}
.person-info__position {
  text-decoration: underline;
  margin: 5px;
}
.person-info button {
  margin-top: 15px;
}
.person-image {
  width: 125px;
  height: 125px;
  border-radius: 50%;
  background: url("../img/photos/photo_2018-12-20_16-23-55.jpg") no-repeat center / cover;
  box-shadow: 0 0 0 2px inset #fff;
}
.upload-cover {
  margin: 0 !important;
}
.cropit-preview {
  /* You can specify preview size in CSS */
  width: 960px;
  height: 540px;
}
